html {
  height: 100%;
}
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.Layout{
    height: 100%;
    /* padding: 35% 5% 35% 5%; */
    padding:0px;
    margin:0px;
}
.backdrop {
  background-color: #666;
  border-radius: 5px;
  position: absolute;
  top: 5%;
  bottom: 5%;
  left: 10%;
  right: 10%;
}

.fade {
  display: none;
}

.fade-appear {
  opacity: 0;
  z-index: 1;
}

.fade-appear.fade-appear-active {
  opacity: 1;
  -webkit-transition: opacity 2000ms linear;
  transition: opacity 2000ms linear;
}

.fade-enter {
  opacity: 0;
  /* z-index: 1; */
}
.fade-enter.fade-enter-active {
  opacity: 1;
  -webkit-transition: opacity 2000ms linear;
  transition: opacity 2000ms linear;
}

.fade-exit {
  opacity: 1;
}
.fade-exit.fade-exit-active {
  opacity: 0;
  -webkit-transition: opacity 2000ms linear;
  transition: opacity 2000ms linear;
}
.fade-exit-done {
  opacity: 0;
}

.Question {
  margin: 1px;
  padding: 30px;
  background-color: #333;
  color: white;
  border-radius: 5px;
}

.Answer button {
  padding: 18px;
  margin: 3px;
  background-color: #333;
  color: #eee;
  box-shadow: none;
  border: none;
}

.Answers {
  padding: 0px;
  margin: 0px;
  margin-left: auto;
  margin-right: auto;
  position: absolute;
  bottom: 15%;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.Questionbox {
  position: absolute;
  text-align: center;
  top: 20%;
  bottom: 15%;
  left: 8%;
  right: 8%;
}

.text-center {
  height: 100%;
}

.App {
  text-align: center;
  /* background-color: #444; */
  height: 100%;
  margin:0;
  padding:0;
}


